.modal-container {
  background-color: rgba(245,254,254,1);
  position: absolute;
  border: 1px solid rgba(204,204,204,1);
  overflow: hidden;
  border-radius: 4px;
  max-height: 80%;
  outline: none;
  padding: 20px;
  z-index: 1031;
}

.modal-container h2 {
  font-size: 1.35rem;
}

.modal-background-image {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: -1;
  opacity: 0.1;
  max-height: 500px;
  -webkit-filter: blur(1px);
          filter: blur(1px);
}
.modal-container {
  top: 10%;
  left: 5%;
  width: 90%;
}

.textarea {
  width: 100%;
}

@media(min-width: 992px) {
  .modal-container {
    top: 50%;
    left: 50%;
    height: auto;
    width: auto;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  .modal-container h2 {
    font-size: 2rem;
  }
}


