.accordion {
  overflow: hidden;
}
.accordion-panel {
  width: 80%;
  margin: auto;
  background-color: rgba(242,254,254,0.95);
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.25);
          box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.25);
}
.accordion-header {
  color: darkblue;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.accordion-header h5 {
  display: inline;
}
.accordion > svg {
  position: absolute;
  color: rgba(242,254,254,1);
}
.accordion-body {
  height: 0px;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  overflow: hidden;
  text-align: justify;
}


